import { createStore } from "vuex";
import axios from "axios";

export default createStore({
  state: {
    infouser: [],
    infoproducts: [],
    fromto:[],
    itemSelect: [],
    filterItems: "",
    listResults: [
      {
        id: 0,
        name: "",
        qty: "",
        disassemble: null,
        assemble: null,
        modal: false
      },
    ],
  },
  mutations: {
    SET_INFOUSER: (state, infouser) => {
      state.infouser = infouser;
    },
    SET_FROMTO: (state, fromto) => {
      state.fromto = fromto;
    },
    SET_INFOPRODUCTS: (state, infoproducts) => {
      state.infoproducts = infoproducts;
    },
    SET_FILTER_VALUE: (state, value) => {
      state.filterItems = value;
    },
    SET_ITEMLIST: (state, value) => {
      state.listResults = value;
    },
    REMOVE_ITEM: (state, index) =>{
      state.listResults.splice(index, 1)
    },
  },
  actions: {
    GET_INFO_FROM_API({ commit }) {
      return axios
        .get("https://bobiora.github.io/cargo/db.json")
        .then((response) => {
          commit("SET_INFOUSER", response.data.infouser);
          commit("SET_FROMTO", response.data.fromto);
          commit("SET_INFOPRODUCTS", response.data.infoproducts);
          return response;
        })
        .catch((error) => {
          console.log(error);
          return error;
        });
    },
    GET_FILTER_VALUE({ commit }, value) {
      commit("SET_FILTER_VALUE", value);
    },
    GET_LIST_ITEM({ commit }, value) {
      commit("SET_ITEMLIST", value);
    },
    DELETE_ITEM({commit}, index){
      commit("REMOVE_ITEM", index)
    },
  },
  getters: {
    INFOUSER(state) {
      return state.infouser;
    },
    FROMTO(state){
      return state.fromto;
    },
    INFOPRODUCTS(state) {
      return state.infoproducts;
    },
    FILTER_VALUE(state) {
      return state.filterItems;
    },
    ITEMLIST(state) {
      return state.listResults;
    },
  },
  modules: {},
});
