<template>
  <div @click="setItem(item_data)" class="items">
    {{ item_data.name }}
  </div>
</template>

<script>
export default {
  name: "Items",
  props: {
    item_data: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  data() {
    return {};
  },
  methods: {
    setItem() {
      this.$emit("setItem", this.item_data);
    },
  },
};
</script>

<style lang="sass">
.items
  font-size: 18px
  padding: 5px 15px
  background-color: #ccc
  border-bottom: 1px solid #fff
  cursor: pointer
  &:hover
    background-color: #eee
</style>
